os espirituales de las plantas cultivadas y sufrir privaciones en el futuro. Por esa razón los excedentes, en el caso del Vaupés, son obsequiados a otra comunidad que no los tiene en una fiesta conocida como el Dabucurí. La acumulación económica es sancionada moralmente. Los seres humanos siempre perturban el orden de la naturaleza mediante el uso arbitrario de los recursos naturales y la transgresión de las normas sociales. Deben, por lo tanto, restablecer el orden perdido que amenaza la naturaleza y la vida social, los rituales, como es el caso del Yuruparí amazónico, se hace para restablecer el orden.")]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("La mediación y relación con los seres espirituales que tienen un dominio sobre los bienes de la naturaleza, a los seres humanos a la flora y a la fauna, en esta relación cultural, los seres espirituales y los chamanes aparecen como los administradores de los flujos de energía y cadenas tróficas que se dan entre los seres vivos.")]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Hay plantas sagradas o de alto valor religioso por razones estrictamente culturales. A manera de ejemplo, entre los embera se habla del árbol genené, origen de los ríos, el Caryocar amygdaliferum -Caryocaraceae. Para los siriano el árbol llamado mochilero, en su lengua U´muy deh, tiene un alto valor religioso y para los sikuani el kaliawiri, asociado al origen de los alimentos cultivados. ")]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Partiendo de lo anterior, en la siguiente tabla se encuentran las plantas asociadas a contextos rituales según sus pueblos: ")])]),_c('div',{staticClass:"col-lg-4"},[_c('img',{staticClass:"img-a img-t",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img39.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClas