entaria hay animales pariente que no deben ser cazados y animales no parientes, el cazador debe realizar ritos y normas precisas, según los estudios etnográficos ya referenciados, estos ritos y normas generalmente son favorables en la preservación de la fauna. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-5 cajon color-acento-botones",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-7"},[_c('p',{staticClass:"mb-0 ps-3"},[_vm._v("El chamán amazónico suele ser denominado “hombre-jaguar”. Se utiliza también una denominación genérica que suele ser precisa: “dueño de maloka”. Es común que se le llame taita o payé –Vaupés–. El chamán, principal especialista del conocimiento tradicional, para acceder al pensamiento como tradición –a la que debe conectarse– usa plantas maestras como la "),_c('i',[_vm._v("Banasteriorsis caapi")]),_vm._v(" el principal bejuco del yagé, ambíl, una resina preparada a partir del tabaco y hojas de coca o el ayuno prolongado. El yagé es utilizado por 72 pueblos amazónicos (Luna, 1986) en su mayoría, del occidente de la cuenca. Este personaje juega un importante papel social ya que sostiene la unidad social y espiritual de la comunidad a la que pertenece (Departamento Nacional de Planeación, DNP, 2004).")])]),_c('div',{staticClass:"col-lg-5 p-0"},[_c('img',{attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img41.jpg"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-5"},[_c('div',{staticClass:"col-lg-3"},[_c('img',{staticClass:"img-a img-t",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img42.svg"),"alt":""}})]),_c('div',{staticClass:"col-lg-9 col-color3",attrs:{"data-aos":"slide-down"}},[_c('div',{staticClass:"bloque-texto-b p-4",attrs:{"data-aos":"fade-down"}},[_c('div',{staticClass:"bloque-texto-b__texto"},[_c('i',{staticClass:"color-green fas fa-quote-left"}),_c('p',[_vm._v("El chamán u hombre-jaguar, debe ser un conocedor del territorio y de los recursos en él existentes. En la medida que una de sus funciones es la de regular el aprovechamiento de los recursos que se extraen del bosque y de negociar con sus dueños espirituales, este conocimiento es vital. Él debe indicarles al cazador y al pescador, donde obtener sus presas y cuándo debe cazar; debe también decirle al cultivador dónde hacer las chagras. Estas actividades, extractivas y agrícolas deben ajustarse a condiciones particulares en correspondencia con ciclos naturales. El chamán posee conocimientos astronómicos, por eso sigue las estrellas y los ciclos solares. Por ejemplo, los chamanes nasa utilizan las montañas de su escarpado territorio, en relación con la salida y puesta del sol para establecer calendarios agrícolas "),_c('br')]),_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("(Departamento Nacional de Planeación, 2004).")])]),_c('i',{staticClass:"color-green fas fa-quote-right"})])])])])}]

